<template>
	<div class="homeSlider">
  		<navTab></navTab>			
		<div class="full">
  		<div class="section">
  			<div class="homeList bgColor">
	  			<div class="title hotAction">热点</div>
	  			<diaryList></diaryList>  				
  			</div>
  			<div class="homeList">
	  			<div class="title pubAction">豆瓣时间</div>
	  			<writerList></writerList>  				
  			</div>
  			<div class="homeList bgColor">
	  			<div class="title pubAction">一刻</div>
	  			<diaryList></diaryList>  				
  			</div>
  			<div class="homeList bgColor">
	  			<div class="title pubAction">发现</div>
	  			<discoverList></discoverList>  				
  			</div>
  			<div class="homeList bgColor">
	  			<div class="title pubAction">为你推荐</div>
	  			<diaryList></diaryList>  				
  			</div>
  			
  		</div>
			
		</div>	
	</div>

</template>

<script>

import $ from 'jquery'
export default {
  name: 'home',
  data: function(){
  	return {
  		T:0
  	}
  },
  components: {
    'navTab': require('../components/navTab.vue'),
    'diaryList': require('../components/diaryList.vue'),
    'writerList': require('../components/writerList.vue'),
    'discoverList': require('../components/discoverList.vue')
  },
  mounted: function(){
	var that = this;
 	touch.on('.homeSlider','swipeup swipedown',function (e) {
		var slider = document.querySelector(".homeSlider");
		e.duration = 0.1;
		e.factor = 1;
		that.T += e.distanceY;
		if (that.T>0) {
			that.T = 0;
		}
		slider.style.top = that.T+"px";
		console.log(that.T,top)	
	}) 	
} 
}
</script>

<style>
.homeSlider{
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
.section{
	width: 100%;
	height: auto;
}
.homeSlider .full{
	width: 100%;
	height: auto;
	position: absolute;
	top: 140px;
}
.homeList{
	width: 100%;
	height: auto;
	padding-top: 16px;
}
.bgColor{
	background-color: white;
}
.title{
	width: 100%;
	height: 24px;
	line-height: 24px;
	padding-left: 10px;
	text-align: left;
	font-size: 16px;
	font-weight: 300;
	box-sizing: border-box;
}
.hotAction{
	border-left: 6px solid #ff8447;
}
.pubAction{
	border-left: 6px solid #42BD56;
}

</style>



