<template>
  <div class="bookBox">
		<nav class="navList">
			<a href="#bookM">电影</a>
			<a href="#bookM?read">读书</a>
			<a href="#bookM?tv">电视</a>
			<a href="#bookM?location">同城</a>
			<a href="#bookM?music">音乐</a>
		</nav>
		<movie></movie>
  </div>
</template>

<script>
export default {
  name: 'book',
  data () {
    return {
      pageIndex: ''
    }
  },
  components: {
    'movie': require('../components/movie.vue')
  },  
  mounted: function(){
		
		window.addEventListener('hashchange',this.clear)
  },
  methods:{
  	clear(){
  		this.pageIndex = (window.location.hash).split('?')[1]; 
  		var as = document.querySelectorAll('.navList a');
	  		for (var i = 0; i < as.length; i++) {
	  			var str = (as[i].href).split('?')[1];
	  			as[i].className = '';
	  			if (str==this.pageIndex) {
	  				as[i].className = 'active'
	  			}
	  		}
  	}
  }
  
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bookBox .navList{
	position: fixed;
	width: 100%;
	height: 36px;
	background-color: white;
	border-top: 1px solid #CDCDCD;
	border-bottom: 1px solid #F2F1EE;
	top: 50px;
	z-index: 999;
}
.bookBox .navList a{
	display: block;
	width: 20%;
	height: 36px;
	line-height: 36px;
	float: left;
	text-align: center;
	color: #B2B2B2;
}
.bookBox  .navList .active{
	color: #42BD56;
	border-bottom: 1px solid #42BD56;
}
</style>



