<template>
	<footer>
		<ul>
			<li v-for='(item, index) in data' :class="{active:item.iscur}" v-on:click="changColor(index)"><i :class="item.pic"></i><span>{{item.name}}</span></li>
		</ul>
	</footer>
</template>

<script>
export default {
  name: 'footer',
  data () {
    return {
      iscur: false,
      data: [
      {path: '#', name: '首页', iscur: true, pic: 'fa fa-home '},
      {path: '#bookM', name: '书影音', iscur: false, pic: 'fa fa-book '},
      {path: '#podcast', name: '广播', iscur: false, pic: 'fa fa-podcast '},
      {path: '#group', name: '小组', iscur: false, pic: 'fa fa-object-group '},
      {path: '#user', name: '我的', iscur: false, pic: 'fa fa-user'}
      ]
    }
  },
  methods: {
    changColor: function (index) {
      for (var i = 0; i < this.data.length; i++) {
        this.data[i]['iscur'] = false
      }
      if (this.data[index].iscur) {
        return
      } else {
        this.data[index].iscur = true
        window.location.hash = this.data[index]['path']
      }
    }
  }
}

</script>

<style>
footer {
  width: 100%;
  height: 52px;
  line-height: 52px;
  position: fixed;
  bottom: 0;
  color: #b2b2b2;
  border-top: 1px solid #e4e4e4;
  padding-top: 8px;
  box-sizing: border-box;
}

.footerColor {
  background-color: #ffffff;
}

.active {
  color: #42bd56;
}

footer ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  overflow: hidden;
}

footer ul li {
  float: left;
  width: 20%;
  text-align: center;
}
footer ul a{
	display: block;
	width: 100%;
	height: 100%;
	overflow: hidden;
	clear: both;
	outline: none;
	text-decoration: none;
	border: none;
	color: #b2b2b2;
}
footer span {
  display: block;
  float: left;
  width: 100%;
  height: 24px;
  line-height: 24px;
  font-size: 14px;  
}
footer li .fa{
  width: 100%;	
  float: left;
}
</style>



