<template>
	<header v-bind:class="{ headerColor: status==0 }">
		<div v-if="status==0">
			<div class="searchBox">
				<i class="fa fa-search"></i>
				<i class="fa fa-barcode"></i>			
				<input type="text"  placeholder="影视 图书 唱片 小组 舞台剧等" />	
			</div>
			<div class="login">
				<i class="pageClass fa fa-comments"></i>
			</div>			
		</div>
		<div v-else-if="status==1">
			<h3>{{title}}</h3>		
			<div class="login">
				<i class="fa fa-comments"></i>
			</div>		
			<div class="search">
				<i class="fa fa-search"></i>
			</div>				
		</div>
		<div v-else-if="status==2">
			<h3>{{title}}</h3>						
			<div class="login">
				<i class="fa fa-comments"></i>
			</div>	
			<div class="user">
				<i class="fa fa-user"></i>
			</div>				
		</div>
		<div v-else-if="status==3">
			<h3>{{title}}</h3>									
			<div class="login">
				<i class="fa fa-comments"></i>
			</div>		
			<div class="search">
				<i class="fa fa-search"></i>
			</div>				
		</div>
		<div v-else-if="status==4">
			<h3>{{title}}</h3>		
			<div class="login">
				<i class="fa fa-cog"></i>
			</div>				
		</div>

	</header>
</template>

<script>
	export default {
	name: 'header',
	data() {
		return {
			status: 0,
			title: '',
			str:''
		}
	},
	mounted:function () {
		window.addEventListener('hashchange',this.hash)
	}
	,
	methods:{
		hash(){
			this.str = (window.location.hash).substr(2);	
			this.hashchange()	    		
		},
		hashchange() {
	
			if(this.str == '') {
				this.status = 0;
			} else if(this.str == 'bookM') {
				this.status = 1;
				this.title = '书影音';
			} else if(this.str == 'podcast') {
				this.status = 2;
				this.title = '推荐广播';
			} else if(this.str == 'group') {
				this.status = 3;
				this.title = '小组';
			} else if(this.str == 'user') {
				this.status = 4;
				this.title = '我的';
			}			
		}		
	}


}
</script>
<style>
	header{
		width: 100%;
		height: 50px;
		line-height: 50px;
		position: fixed;
		left: 0;
		top: 0;
		background-color:#ffffff;
	}
	.headerColor{
		background-color:#42bd56;
	}
	.pageClass{
		color: white;
	}
	header .searchBox{
		margin: 7px 0 7px 10px;
		width: 80%;
		height: 36px;
		line-height: 36px;
		border-radius: 3px;
		background-color: white;
		overflow: hidden;
		position: absolute;
		top: 0;		
		color: #E4E4E4;
		font-weight: 300;
		font-size: 16px;
	}
	header .searchBox i:nth-of-type(1){
		position: absolute;
		width: 36px;
		height: 36px;
		line-height: 36px;
		left: 0;
		top: 0;		
	}
	header .searchBox i:nth-of-type(2){
		position: absolute;
		width: 36px;
		height: 36px;
		line-height: 36px;	
		right: 0;			
	}
	header .searchBox input{
		display: block;
		margin: 0;
		padding: 0;
		width: 100%;
		height: 36px;
		line-height: 36px;
		padding-left: 36px;
		box-sizing: border-box;
		outline: none;
		border: none;
	}
	header .login,header .search,header .user{
		width: 36px;
		height: 50px;
		margin-right: 8px;
		float: right;
		color: white;
		font-size: 18px;
		color: #42bd56;		
	}
	header h3{
		position: absolute;
		width: 100px;
		height: 50px;
		line-height: 50px;
		left: 50%;
		margin-left: -50px;
		font-size: 18px;
		text-align: center;
	}
</style>




